import Star from 'vue-material-design-icons/Star';
import VideoIcon from 'vue-material-design-icons/Video.vue';
import { generateUrl } from '@nextcloud/router';
import { NcCheckboxRadioSwitch } from '@nextcloud/vue';
import Semaphore from '../utils/semaphoreWithPriority.js';
export default {
  name: 'File',
  components: {
    NcCheckboxRadioSwitch,
    Star,
    VideoIcon
  },
  inheritAttrs: false,
  props: {
    file: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      required: true
    },
    allowSelection: {
      type: Boolean,
      default: true
    },
    visibility: {
      type: String,
      required: true
    },
    semaphore: {
      type: Semaphore,
      required: true
    }
  },

  data() {
    return {
      loadedNear: false,
      loadedVisible: false,
      errorNear: false,
      errorVisible: false,
      canLoad: false,
      semaphoreSymbol: null,
      isDestroyed: false
    };
  },

  computed: {
    /** @return {string} */
    ariaDescription() {
      return `image-description-${this.file.fileid}`;
    },

    /** @return {string} */
    ariaLabel() {
      return t('photos', 'Open the full size "{name}" image', {
        name: this.file.basename
      });
    },

    /** @return {boolean} */
    isImage() {
      return this.file.mime.startsWith('image');
    },

    /** @return {string} */
    decodedEtag() {
      return this.file.etag.replace('&quot;', '').replace('&quot;', '');
    },

    /** @return {string} */
    srcVisible() {
      return this.getItemURL(512);
    },

    /** @return {string} */
    srcNear() {
      return this.getItemURL(64);
    }

  },

  async mounted() {
    this.semaphoreSymbol = await this.semaphore.acquire(() => {
      switch (this.visibility) {
        case 'visible':
          return 1;

        case 'near':
          return 2;

        default:
          return 3;
      }
    }, this.file.fileid);

    if (this.visibility === 'none' || this.isDestroyed) {
      this.releaseSemaphore();
      return;
    }

    this.canLoad = true;
  },

  beforeDestroy() {
    this.isDestroyed = true;
    this.releaseSemaphore(); // cancel any pending load

    if (this.$refs.imgNear !== undefined) {
      this.$refs.imgNear.src = '';
    }

    if (this.$refs.srcVisible !== undefined) {
      this.$refs.srcVisible.src = '';
    }
  },

  methods: {
    emitClick() {
      this.$emit('click', this.file.fileid);
    },

    /** When the 'near' image is fully loaded by browser we release semaphore */
    onLoadNear() {
      this.loadedNear = true;
      this.releaseSemaphore();
    },

    /** When the 'visible' image is fully loaded by browser we release semaphore */
    onLoadVisible() {
      this.loadedVisible = true;
      this.releaseSemaphore();
    },

    onErrorNear() {
      this.errorNear = true;
      this.releaseSemaphore();
    },

    onErrorVisible() {
      this.errorVisible = true;
      this.releaseSemaphore();
    },

    onToggle(value) {
      this.$emit('select-toggled', {
        id: this.file.fileid,
        value
      });
    },

    getItemURL(size) {
      const token = this.$route.params.token;

      if (token) {
        return generateUrl(`/apps/photos/api/v1/publicPreview/${this.file.fileid}?etag=${this.decodedEtag}&x=${size}&y=${size}&token=${token}`);
      } else {
        return generateUrl(`/apps/photos/api/v1/preview/${this.file.fileid}?etag=${this.decodedEtag}&x=${size}&y=${size}`);
      }
    },

    releaseSemaphore() {
      if (this.semaphoreSymbol === null) {
        return;
      }

      this.semaphore.release(this.semaphoreSymbol);
      this.semaphoreSymbol = null;
    }

  }
};